import React, { Component } from 'react';
import Layout from '../layout/layout';
import './searchResults.scss';
import icThumbnail from '../../assets/img/thumbnail-no-img2.png';
import icThumbnail1 from '../../assets/img/resultSearch01.jpg';

const listResultSearch = [
  {
    title: "Chng khon phi sinh: Li ch v im khc bit",
    image: icThumbnail1,
    des: "Vic giao dch chng khon phi sinh s gip nh u t phng h ri ro (Hedging) khi c nhu cu giao dch thc s ti sn c s v phng h ri ro lin quan n bin ng gi ca ti sn.",
  },
  {
    title: "M ti khon chng khon phi sinh cn bao nhiu tin? ",
    image: icThumbnail,
    des: "Nh u t cn chun b bao nhiu tin  m ti khon chng khon phi sinh v np tin k qu ban u trc khi tham gia mua bn hp ng tng lai.",
  },
  {
    title: "Cch thc m ti khon chng khon phi sinh",
    image: icThumbnail,
    des: "  tham gia vo cc hot ng giao dch trn th trng CKPS th vic u tin NT cn thc hin l m ti khon phi sinh. Bi vit sau y s gii thiu chi tit cc cch m ti khon CKPS dnh cho NT c nhn trong nc.",
  },
  {
    title: " Hng dn cch chi chng khon phi sinh ",
    image: icThumbnail,
    des: "  tham gia vo cc hot ng giao dch trn th trng CKPS th vic u tin NT cn thc hin l m ti khon phi sinh. Bi vit sau y s gii thiu chi tit cc cch m ti khon CKPS dnh cho NT c nhn trong nc.",
  },
  {
    title: "Ba chin lc u t trong chng khon phi sinh",
    image: icThumbnail,
    des: "  tham gia vo cc hot ng giao dch trn th trng CKPS th vic u tin NT cn thc hin l m ti khon phi sinh. Bi vit sau y s gii thiu chi tit cc cch m ti khon CKPS dnh cho NT c nhn trong nc.",
  }
]

export default class searchResults extends Component {
  render() {
    return (
      <Layout>
        <div className='search_Results_page'>
          <div className='searchResults_Banner'>
            <h3>Tm kim</h3>
          </div>
          <div className='mainContent'>
            <div className='header_title'>
              <div className='searchresult_title'>Kt qu tm kim</div>
              <hr />
            </div>
            <div className='listSearchResults'>
              <ul>
                {
                  listResultSearch.map((item, index) => {
                    return <li key={index}>
                      <div className='thumb'>
                        <a href="/news"><img alt="" src={item.image ? item.image : icThumbnail} /></a>
                      </div>
                      <div className='des'>
                        <a href="/news">
                          <b>{item.title}</b>
                        </a>
                        {
                          item.des
                        }
                      </div>
                    </li>
                  })
                }
              </ul>
            </div>
          </div>
        </div>
      </Layout>
    )
  }
}
