import React, { Component } from 'react';
// import Layout from '../layout/layout';
import './signIn.scss';
import imgBgLeft from '../../assets/img/bg_SignIn.jpg';
import logoMbs from '../../assets/img/logo-blue.png';

export default class signIn extends Component {
  render() {
    return (
      <div className='signin_page'>
        <div className='contentForm'>
          <div className='leftF'>
            <img src={imgBgLeft} alt="ng nhp" />
            <div className='titleSignin top14'>ng Nhp</div>
            <div class="checkTKCK"><a href="/" alt="Trang ch" className='logo'><img src={logoMbs} alt=""/></a>
            <p className="copyRight">Copyrights 2000 - 2019 MBS.</p>
            </div>
          </div>
          <div className='rightF'>
            <form class="form-detail" action="#" method="post" id="myform">
              <div class="form-row">
                <label for="your_email">Tn ng nhp/E-MAIL</label>
                <input type="text" name="your_email" id="your_email" class="input-text" required pattern="[^@]+@[^@]+.[a-zA-Z]{2,6}" />
              </div>
              <div class="form-row">
                <label for="password">Mt khu</label>
                <input type="password" name="password" id="password" class="input-text" required />
              </div>
              <div class="form-row-check txtRight">
                <div className='checkTKCK'>
                  <label><a className='txt14' href="./forgot-pass-word">Bn qun mt khu?</a> </label>
                </div>
              </div>
              <div class="form-row-last">
                <button className='register'>
                  <span>ng Nhp</span>
                </button>
                <p>Bn cha c ti khon? <a href="./sign-up">ng k</a></p>
              </div>
            </form>
          </div>
        </div>
      </div>
    )
  }
}
