import React, { Component } from 'react';
import './contactBox.scss';

export default class ContactBox extends Component {
  render() {
    return(
      <div className='contact-box'>
        <div className='info-box'>
          <div className='contact-title'>PHNG DCH V KHCH HNG ONLINE</div>
          <div className='info-content'>
            <i className="fas fa-map-marker-alt"></i>
            <span className='info-address'>S 3 Liu Giai, Qun Ba nh, H Ni</span>
          </div>
          <div className='info-content'>
            <i className="fas fa-phone"></i>
            <span className='info-hotline'>1900 9088</span>
          </div>
          <div className='info-content'>
            <i className="fas fa-envelope"></i>
            <span className='info-email'>cskh@mbs.com.vn</span>
          </div>
        </div>
        <div className='msg-box'>
          <div className='contact-title'>GI LI NHN</div>
          <input className='form-control msg-text' type='text' placeholder='H v tn *'/>
          <input className='form-control msg-text' type='text' placeholder='in thoi *'/>
          <input className='form-control msg-text' type='text' placeholder='Email *'/>
          <textarea className="form-control msg-text" rows="3" placeholder='Ni dung *'/>

          <button className='btn btn-send-msg'>
            <i className="fas fa-paper-plane"></i>
            GI
          </button>
        </div>
      </div>
    );
  }
}