import React, { Component } from 'react';
import { connect } from 'react-redux';
import Layout from '../../layout/layout';
import './trainingService.scss';
import icNoImg from '../../../assets/img/ic_no_img2.png';
import Slider from "react-slick";
import actions from '../../../store/trainingService/actions';
import notifyActions from '../../../store/notification/actions';
import { toast } from 'react-toastify';
import { TYPE_COURSE, CATEGORY_COURSE } from '../../../utils/constant';
import RegisterPopup from './registerPopup';
import { currency } from '../../../utils/currency';

const listTeachers = [
  {
    img: '',
    name: 'NGUYN TH MINH TH',
    title: 'CEO & FOUNDER',
    detail: 'Hn 5 nm kinh nghim phn tch kinh t v m v tri phiu'
  },
  {
    img: '',
    name: 'NGUYN TH MINH TH',
    title: 'CEO & FOUNDER',
    detail: 'Hn 5 nm kinh nghim phn tch kinh t v m v tri phiu'
  },
  {
    img: '',
    name: 'NGUYN TH MINH TH',
    title: 'CEO & FOUNDER',
    detail: 'Hn 5 nm kinh nghim phn tch kinh t v m v tri phiu'
  },
  {
    img: '',
    name: 'NGUYN TH MINH TH',
    title: 'CEO & FOUNDER',
    detail: 'Hn 5 nm kinh nghim phn tch kinh t v m v tri phiu'
  }
]

class TrainingService extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedCourse: 1,
      selectedTypeCourse: 1,
      activeCourse: 1,
      showPopup: false,
      objCourse: null,
      showMore: null
    }
  }

  onShowMore = (index) => {
    let nextShow = null;
    if (this.state.showMore !== index) {
      nextShow = index;
    }
    this.setState(state => ({
      showMore: nextShow
    }))
  }
  onChangeSelectCourse = (type) => {
    this.setState({
      selectedCourse: type
    })
  }
  onChangeSelectTypeCourse = (index) => {
    this.setState({
      selectedTypeCourse: index
    })
  }
  onChangeSelectHotCourse = (index) => {
    this.setState({
      activeCourse: index
    })
  }
  onRegister = (course) => {
    this.setState({
      showPopup: true,
      objCourse: course
    })
  }
  onCloseRegister = () => {
    this.setState({
      showPopup: false,
      objCourse: null
    })
  }
  onSubmitRegister = (data) => {
    this.props.registerCourse(data);
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.message !== '' && nextProps.message !== this.props.message) {
      toast(nextProps.message);
      this.props.clearNotify();
    }
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevState.selectedCourse !== this.state.selectedCourse) {
      this.props.fetchlistCourseTop(this.state.selectedCourse);
    }
    if (prevState.selectedTypeCourse !== this.state.selectedTypeCourse) {
      this.props.fetchlistCourseCategory(this.state.selectedTypeCourse);
    }
  }

  componentDidMount() {
    const state = this.state;
    this.props.fetchListCourseHot(1);
    this.props.fetchlistCourseTop(state.selectedCourse);
    this.props.fetchlistCourseCategory(state.selectedTypeCourse);
  }

  render() {
    const {
      selectedCourse,
      selectedTypeCourse,
      activeCourse,
      showPopup,
      objCourse,
      showMore
    } = this.state;
    const props = this.props;
    var settings = {
      dots: true,
      arrows: true,
      infinite: true,
      speed: 500,
      slidesToShow: 3,
      centerMode: true
    };
    
    return (
      <Layout title="">
        {
          <RegisterPopup isShowModal={showPopup} title="Tiu " courseInfo={objCourse}
            closePopup={this.onCloseRegister} onSubmit={this.onSubmitRegister}/>
        }
        <div className='training-service-page'>
          <div className='banner'>
            <div className='title-banner'>DCH V O TO</div>
          </div>
          <div className='course-layout'>
            <div className='course-box'>
              <div className='course-content'>
                <div className='list-course-box'>
                  <div className='select-course-type'>
                    {
                      TYPE_COURSE.map((item, index) => {
                        return <div key={index} className={selectedCourse === item.type ? 'type-course active' : 'type-course'}
                          onClick={() => this.onChangeSelectCourse(item.type)} >{item.name}</div>
                      })
                    }
                  </div>
                  <div className='list-course'>
                    {
                      props.listCourseTop.map((item, index) => {
                        return <div key={index} className='course-item'>
                          <div className='time'>KHAI GING<br /><span>{item.startDate}</span></div>
                          <div className='title'>{item.name}</div>
                          <div className='box-left'>
                            <div className='course-title'>Thi gian: <span>{item.schedule}</span></div>
                            <div className='course-title'>Ging vin: <span>{item.teacher}</span></div>
                            <div className='course-title'>a im: <span>{item.address}</span></div>
                          </div>
                          <div className='box-right'>
                            <div className='course-title'>Khai ging: <span>{item.startDate}</span></div>
                            <div className='course-title'>Hnh thc hc: <span>
                            {
                              TYPE_COURSE.map(course => {
                                if (item.type === course.type) {
                                  return course.name;
                                }
                                return null;
                              })
                            }
                            </span></div>
                            <div className='course-title'>Chi ph: <span>{currency(item.fee)} VN</span></div>
                          </div>
                          <div className={showMore === index ? 'course-des show-more' : 'course-des'}>{item.description}</div>
                          <div><i className={showMore === index ?'fas icSubMore': 'fas icAdMore'} onClick={() => this.onShowMore(index)}></i></div>
                          <div className='course-footer'>

                            File download: <a className='file-download' href={item.url}><i className="fas icPdf"></i>
                            </a>
                            <div className='url-register'>Click tham gia: <span><span className="regLean" onClick={() => this.onRegister(item)}>ng k hc</span></span></div>
                          </div>
                        </div>
                      })
                    }
                  </div>
                </div>
                <div className='hot-course'>
                  <div className='hot-title'>KHA HC HOT</div>
                  {
                    props.listCourseHot.map((item, index) => {
                      return <React.Fragment key={index}>
                        {
                          activeCourse === index ? <div className='course-item active' key={index}>

                            <div className='course-name' onClick={() => this.onChangeSelectHotCourse(index)}>
                              <div className='course-index'>{index + 1}</div>
                              <span>{item.name}</span></div>
                            <div className='box-left'>
                              <div className='course-title'>Thi gian: <span>{item.schedule}</span></div>
                              <div className='course-title'>Ging vin: <span>{item.teacher}</span></div>
                              <div className='course-title'>a im: <span>{item.address}</span></div>
                            </div>
                            <div className='box-right'>
                              <div className='course-title'>Khai ging: <span>{item.startDate}</span></div>
                              <div className='course-title'>Hnh thc hc: <span>
                                {
                                  TYPE_COURSE.map(course => {
                                    if (item.type === course.type) {
                                      return course.name;
                                    }
                                    return null;
                                  })
                                }
                              </span></div>
                              <div className='course-title'>Chi ph: <span>{currency(item.fee)} VN</span></div>
                            </div>
                            <div className='course-des'>{item.des}<i className="fas icAdMore"></i></div>
                            <div className='course-footer'>

                              File download: <a className='file-download' href={item.url}><i className="fas icPdf"></i>
                              </a>
                              <div className='url-register'>Click tham gia: <span>
                                  <span className="regLean" onClick={() => this.onRegister(item)}>ng k hc</span></span></div>
                            </div>
                          </div> :
                            <div className='course-item' onClick={() => this.onChangeSelectHotCourse(index)}>
                              <div className='course-name' ><div className='course-index'>{index + 1}</div>{item.title}</div>
                            </div>
                        }
                      </React.Fragment>
                    })
                  }
                </div>
              </div>
            </div>
          </div>
          <div className='type-course-layout'>
            <div className='title'>DANH SCH CC KHA HC</div>
            <hr />
            <div className='select-type-course'>
              {
                CATEGORY_COURSE.map((item, index) => {
                  return <div key={index} className={selectedTypeCourse === item.cat ? 'item-type-course selected' : 'item-type-course'}
                    onClick={() => this.onChangeSelectTypeCourse(item.cat)}>{item.name}
                  </div>
                })
              }
            </div>
            <div className="type-course-list">
              {
                props.listCourseCategory.map((course, index) => {
                  return <div className='item-course' key={index}>
                    <div className='course-index'>{index + 1}</div>
                    <div className='sqrL'></div>
                    <div className='course-name'>{course.name}</div>
                    <div className='course-date-time-cost'>Thi gian: <span>{course.schedule}</span></div>
                    <div className='course-date-time-cost'>Khai ging: <span>{course.startDate}</span></div>
                    <div className='course-date-time-cost'>Chi ph: <span>{currency(course.fee)} VN</span></div>
                    <div className='course-des'>{course.description}</div>
                    <div className='blInfo'>
                      <table>
                        <thead>
                          <tr>
                            <th scope='col'>Khai ging</th>
                            <th scope='col'>Ging vin</th>
                            <th scope='col'>Hnh thc hc</th>
                            <th scope='col'>a im</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td>{course.startDate}</td>
                            <td>{course.teacher}</td>
                            <td>
                              {
                                  TYPE_COURSE.map(item => {
                                    if (item.type === course.type) {
                                      return item.name;
                                    }
                                    return null;
                                  })
                                }
                            </td>
                            <td>{course.address}</td>
                          </tr>
                        </tbody>
                      </table>

                      <div className='file-download'>
                        File download:
                    <a href={course.urlFile}><i className="fas icExcel"></i></a>
                      </div>
                      <div className='url-register'> tham gia bn click: <span className='btn_regis' onClick={() => this.onRegister(course)}>ng k hc</span></div>
                    </div>
                  </div>
                })
              }
            </div>
          </div>
          <div className='teachers-layout'>
            <div className='teachers-title'>I NG GING VIN</div>
            <hr />
            <div className='teacher-des'>i ng ging vin ca MBS tp hp nhng chuyn gia hng u trn th trng chng khon vi kho kin thc su rng cng kinh nghim giao dch lu nm</div>
            <div className='teacher-special'>CHUYN GIA T VN</div>
            <Slider {...settings}>
              {
                listTeachers.map((item, index) => {
                  return <div key={index}>
                    <img src={item.img ? item.img : icNoImg} alt={`img-${index}`} />
                    <div className='teacher-name'>{item.name}</div>
                    <div className='teacher-title'>{item.title}</div>
                    <div className='teacher-detail'>{item.detail}</div>
                  </div>
                })
              }
            </Slider>
          </div>
        </div>
      </Layout>
    )
  }
}

const mapStateToProps = state => {
  return {
    listCourseHot: state.TrainingService.listCourseHot,
    listCourseCategory: state.TrainingService.listCourseCategory,
    listCourseTop: state.TrainingService.listCourseTop,
    total: state.TrainingService.total,
    detail: state.TrainingService.detail,
    message: state.Notifys.message
  };
};

const mapDispatchToProps = dispatch => {
  return {
    fetchListCourse: (start, limit, type, category, priority) => {
      dispatch(actions.listCourse(start, limit, type, category, priority));
    },
    fetchListCourseHot: (priority) => {
      dispatch(actions.listCourseHot(priority));
    },
    fetchlistCourseTop: (type) => {
      dispatch(actions.listCourseTop(type));
    },
    fetchlistCourseCategory: (category) => {
      dispatch(actions.listCourseCategory(category));
    },
    getDetail: (id) => {
      dispatch(actions.getDetail(id));
    },
    registerCourse: (data) => {
      dispatch(actions.registerCourse(data));
    },
    clearNotify: () => {
      dispatch(notifyActions.clearNotify());
    }
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(TrainingService);